.CartPage {
    height: 100%;
    display: flex;
    flex-direction: column;
    /* display: grid;
    grid-template-columns: 1.6fr 3.5fr 3fr;
    grid-template-rows: 1fr; */
    background-color: var(--white);
    border-radius: 2vmin;
    justify-content: center;
    align-items: center;
}

.CartPage .aside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 3vmin 2vmin;
}

.CartPage div{
    width: 100%;
}